.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 20rem;
  margin-top: 5rem;
}

.content {
  flex: 1 1 50%;
}

.home {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.name {
  font-size: 3rem;
  font-weight: 700;
  color: rgb(255, 127, 38);
}

.job {
  font-size: 3rem;
  color: white;
}

.infoHome p,
.infoHome a {
  color: #eee;
  margin: 0.25rem 0;
  margin-top: 2rem;
}

.column {
  margin-top: 2rem;
}

h1 {
  margin-bottom: 0.1rem;
}

.socials {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.btn {
  background-color: #1d4ed8;
  border: none;
  height: 4rem;
  width: 12rem;
  margin: 0.7rem;
  padding: 1rem 2rem;
  border-radius: 5px;
  cursor: pointer;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.btn:hover {
  background-color: #1e40af;
}

.image {
  flex: 1 1 50%;
  max-width: 1400px;
  margin-top: 3rem;
}

.image img {
  width: 100%;
}

.socials a {
  color: #fff;
  background-color: #1d4ed8;
  padding: 5px 10px;
  font-size: 1.5rem;
  border-radius: 100%;
  transition: all 0.3s ease;
  text-decoration: none;
}

.socials a:hover {
  background-color: #1e40af;
  transition: all 0.3s ease;
}

.toeic:hover {
  cursor: pointer;
  color: rgb(255, 127, 38);
  transition: all 0.3s ease;
}

/* Modal */
.languageModal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.8);
  background-color: #27272a;
  padding: 2rem;
  border-radius: 12px;
  color: white;
  width: 300px;
  text-align: center;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease, transform 0.3s ease;
  z-index: 1001;
}

.languageModal.show {
  opacity: 1;
  pointer-events: auto;
  transform: translate(-50%, -50%) scale(1);
}

.languageModalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
  z-index: 1000;
}

.languageModalOverlay.show {
  opacity: 1;
  pointer-events: auto;
}

.languageModalOverlay.hideOverlay {
  opacity: 0;
  pointer-events: none;
}
